html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input {
  box-sizing: border-box;
}

input:focus {
  outline: none;
}

a {
  color: #000;
  text-decoration: none;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "Mona-Sans";
  src: url("/public/font/Mona-Sans/Mona-Sans.woff2");
}
:root {
  --white: #f0f6fc;
  --gray: #9198a1;
}

body {
  background-color: #0d1117;
  font-family: "Mona-Sans";
}

.inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 1500px;
  height: 100%;
  margin: auto;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
}
header .logo-wrap {
  margin-right: 40px;
}
header nav ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 24px;
}
header nav ul a {
  font-size: 20px;
  font-weight: 400;
  color: var(--white);
}

.info-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-image: url("/public/images/profile-bg.png");
  background-size: 63%;
  background-repeat: no-repeat;
  background-position: 175%;
  overflow: hidden;
}
.info-wrap .inner {
  align-items: flex-start;
  flex-direction: column;
  width: 1400px;
  margin-top: 250px;
}
.info-wrap .inner .greeting-wrap {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 24px;
  margin-left: 130px;
  margin-bottom: 100px;
}
.info-wrap .inner .greeting-wrap .greeting-main {
  font-size: 63px;
  font-weight: 800;
  color: var(--white);
}
.info-wrap .inner .greeting-wrap .greeting-sub {
  font-size: 24px;
  color: var(--gray);
}
.info-wrap .inner .github-address {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  margin-left: 130px;
  margin-bottom: 100px;
}
.info-wrap .inner .github-address--title {
  margin-bottom: 12px;
  font-size: 14px;
  color: var(--white);
}
.info-wrap .inner .github-address--box-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.info-wrap .inner .github-address--box-wrap .github-address-box {
  width: 500px;
  height: 48px;
}
.info-wrap .inner .github-address--box-wrap .github-address-box input {
  width: 60%;
  height: 100%;
  padding: 16px;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  font-size: 16px;
}
.info-wrap .inner .github-address--box-wrap .github-address-box button {
  width: 40%;
  height: 100%;
  background-color: #6e40c9;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  font-size: 16px;
  font-weight: 800;
  color: var(--white);
}
.info-wrap .inner .github-address--box-wrap .mid-line {
  width: 0.5px;
  height: 48px;
  margin: 0 24px;
  background-color: #3d444d;
}
.info-wrap .inner .github-address--box-wrap .resume-btn {
  position: relative;
  width: 275px;
  height: 48px;
  background-color: transparent;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 800;
  color: var(--white);
  box-shadow: #bc8cff 0 0 0 1px inset;
  transition: all 0.3s;
}
.info-wrap .inner .github-address--box-wrap .resume-btn:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 55px;
  transform: translateY(-50%) rotate(-45deg);
  width: 9px;
  height: 9px;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  transition: all 0.3s;
}
.info-wrap .inner .github-address--box-wrap .resume-btn:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 55px;
  transform: translateY(-50%);
  width: 15px;
  height: 1.2px;
  background-color: #fff;
  transition: all 0.3s;
}
.info-wrap .inner .github-address--box-wrap .resume-btn:hover {
  box-shadow: #fff 0 0 0 1px inset;
}
.info-wrap .inner .github-address--box-wrap .resume-btn:hover:before {
  right: 45px;
}
.info-wrap .inner .github-address--box-wrap .resume-btn:hover:after {
  right: 45px;
}
.info-wrap .inner .portfolio-icon--wrap {
  margin-left: 130px;
  font-size: 20px;
  color: var(--gray);
}
.info-wrap .inner .portfolio-icon--wrap .portfolio-icon--text {
  margin-bottom: 48px;
}
.info-wrap .inner .portfolio-icon--wrap .icon-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 80px;
  width: 100%;
  height: 70px;
}
.info-wrap .inner .portfolio-icon--wrap .icon-list .icon {
  font-size: 56px;
  filter: grayscale(1);
  transition: all 0.3s;
  cursor: pointer;
}
.info-wrap .inner .portfolio-icon--wrap .icon-list .icon:hover {
  font-size: 64px;
  filter: grayscale(0);
}/*# sourceMappingURL=style.css.map */