.info-wrap{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-image: url('/public/images/profile-bg.png');
    background-size: 63%;
    background-repeat: no-repeat;
    background-position: 175%;
    overflow: hidden;

    .inner{
        align-items: flex-start;
        flex-direction: column;
        width: 1400px;
        margin-top: 250px;

        .greeting-wrap{
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            gap: 24px;
            margin-left: 130px;
            margin-bottom: 100px;

            .greeting-main{
                font-size: 63px;
                font-weight: 800;
                color: var(--white);
            }
            .greeting-sub{
                font-size: 24px;
                color: var(--gray);
            }
        }
        .github-address{
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            margin-left: 130px;
            margin-bottom: 100px;

            &--title{
                margin-bottom: 12px;
                font-size: 14px;
                color: var(--white);
            }
            &--box-wrap{
                display: flex;
                align-items: center;
                justify-content: flex-start;

                .github-address-box{
                    width: 500px;
                    height: 48px;

                    input{
                        width: 60%;
                        height: 100%;
                        padding: 16px;
                        border-top-left-radius: 6px;
                        border-bottom-left-radius: 6px;
                        font-size: 16px;
                    }
                    button{
                        width: 40%;
                        height: 100%;
                        background-color: #6e40c9;
                        border-top-right-radius: 6px;
                        border-bottom-right-radius: 6px;
                        font-size: 16px;
                        font-weight: 800;
                        color: var(--white);
                    }
                }
                .mid-line{
                    width: 0.5px;
                    height: 48px;
                    margin: 0 24px;
                    background-color: #3d444d;
                }
                .resume-btn{
                    position: relative;
                    width: 275px;
                    height: 48px;
                    background-color: transparent;
                    border-radius: 6px;
                    font-size: 16px;
                    font-weight: 800;
                    color: var(--white);
                    box-shadow: #bc8cff 0 0 0 1px inset;
                    transition: all 0.3s;
                    
                    &:before{
                        content: '';
                        position: absolute;
                        top: 50%;
                        right: 55px;
                        transform: translateY(-50%) rotate(-45deg);
                        width: 9px;
                        height: 9px;
                        border-right: 1px solid #fff;
                        border-bottom: 1px solid #fff;
                        transition: all 0.3s;
                    }
                    &:after{
                        content: '';
                        position: absolute;
                        top: 50%;
                        right: 55px;
                        transform: translateY(-50%);
                        width: 15px;
                        height: 1.2px;
                        background-color: #fff;
                        transition: all 0.3s;
                    }
                    &:hover{
                        box-shadow: #fff 0 0 0 1px inset;
                        
                        &:before{
                            right: 45px;
                        }
                        &:after{
                            right: 45px;;
                        }
                    }
                }
            }
        }
        .portfolio-icon--wrap{
            margin-left: 130px;
            font-size: 20px;
            color: var(--gray);

            .portfolio-icon--text{
                margin-bottom: 48px;
            }
            .icon-list{
                display: flex;
                align-items: center;
                justify-content: flex-start;
                gap: 80px;
                width: 100%;
                height: 70px;

                .icon{
                    font-size: 56px;
                    filter: grayscale(1);
                    transition: all 0.3s;
                    cursor: pointer;

                    &:hover{
                        font-size: 64px;
                        filter: grayscale(0);
                    }
                }
            }
        }
    }
}